import style from "./style.module.css";
import classNames from "classnames";
import { Tab } from "@ya.praktikum/react-developer-burger-ui-components/dist/ui/tab";
import { useEffect, useState } from "react";
import { Category } from "../category/category";
import { useSelector } from "react-redux";
import { useDispatch } from '../../services/hooks';
import { useInView } from "react-intersection-observer";
import { RootStore } from "../../services/store";

export const BurgerIngredients = () => {
  const [current, setCurrent] = useState("buns");
  const dispatch = useDispatch();

  const ingredients = useSelector((state:RootStore) => state.ingredientsStore.data);

  const buns = ingredients.filter((item) => item.type === "bun");

  const main = ingredients.filter((item) => item.type === "main");
  const sauce = ingredients.filter((item) => item.type === "sauce");

  function handleOnClickTab(tab: string) {
    setCurrent(tab);
    const title = document.getElementById(tab);
    if (title) title.scrollIntoView({ behavior: "smooth" });
  }
  const [refSauce, inViewSauce] = useInView();
  const [refMain, inViewMain] = useInView();
  const [refBuns, inViewBuns] = useInView();

  useEffect(() => {
    if (inViewBuns) {
      setCurrent("buns");
    } else if (inViewSauce) {
      setCurrent("sauce");
    } else if (inViewMain) {
      setCurrent("main");
    }
  }, [inViewBuns, inViewMain, inViewSauce]);

  return (
    <div className={style.ingredients}>
      <div className={style.menu}>
        <Tab
          value="buns"
          active={current === "buns"}
          onClick={handleOnClickTab}
        >
          
        </Tab>
        <Tab
          value="main"
          active={current === "main"}
          onClick={handleOnClickTab}
        >
          
        </Tab>
        <Tab
          value="sauce"
          active={current === "sauce"}
          onClick={handleOnClickTab}
        >
          
        </Tab>
      </div>
      <div className={classNames(style.wrapper, "custom-scroll")}>
        <Category title="" id="buns" ingredients={buns} ref={refBuns} />
        <Category title="" id="main" ingredients={main} ref={refMain} />
        <Category title="" id="sauce" ingredients={sauce} ref={refSauce} />
      </div>
    </div>
  );
};
